body {
  height: 100vh;
  background: url("./assets/bg-picture.jpg") no-repeat center;
  background-size: cover;
  text-align: center;
  font-family: "Roboto", sans-serif;
}

li {
  list-style: none;
}

ul {
  padding: 0px;
  margin: 0px;
}

header {
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
h1 {
  padding: 25px;
  font-size: 72px;
  background-color: rgba(162, 49, 38, 0.75);
  border: white 5px solid;
}

input[type="text"] {
  color: #333;
  font-size: 1.2rem;
  margin: 0 auto;
  padding: 1.5rem 2rem;
  border-radius: 0.2rem;
  background-color: rgb(255, 255, 255);
  border: none;
  width: 30%;
  display: block;
  border-bottom: 0.3rem solid transparent;
  transition: all 0.3s;
}
